.list {
  width: 400px;
  padding: 12px;
}

.list_item {
  display: flex;
  align-items: center;
  padding: 12px;

  box-shadow: 0px 2px 1px -1px rgba(0, 0, 0, 0.2),
    0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 1px 3px 0px rgba(0, 0, 0, 0.12);
}

.list_button {
  margin-right: 4px;
  margin-left: 4px;
}
